import React from 'react';
import { MoreHorizontal } from 'lucide-react';

const mockRequests = [
  { id: 1, name: "Tyrell Barrows", avatar: "https://picsum.photos/40/40?random=1", status: "wants to add you to friends" },
  { id: 2, name: "Selena Gomez", avatar: "https://picsum.photos/40/40?random=2", status: "wants to add you to friends" },
];

const mockContacts = [
  { id: 101, name: "Amanda Miles", avatar: "https://picsum.photos/40/40?random=3" },
  { id: 102, name: "Melissa Byron", avatar: "https://picsum.photos/40/40?random=4" },
  { id: 103, name: "Ronald Bezos", avatar: "https://picsum.photos/40/40?random=5", unread: 3 },
  { id: 104, name: "Billy Rosewood", avatar: "https://picsum.photos/40/40?random=6" },
  { id: 105, name: "Katty Monroe", avatar: "https://picsum.photos/40/40?random=7", unread: 2 },
  { id: 106, name: "Kurt Williamson", avatar: "https://picsum.photos/40/40?random=8" },
  { id: 107, name: "Sarah Whitman", avatar: "https://picsum.photos/40/40?random=9" },
  { id: 108, name: "Alena Vinkova", avatar: "https://picsum.photos/40/40?random=10" },
  { id: 109, name: "Zoe Hart", avatar: "https://picsum.photos/40/40?random=11" },
  { id: 110, name: "David Chen", avatar: "https://picsum.photos/40/40?random=12" },
  { id: 111, name: "Eve Dupont", avatar: "https://picsum.photos/40/40?random=13" },
  { id: 112, name: "Frank Black", avatar: "https://picsum.photos/40/40?random=14" },
];

interface ContactItemProps {
  name: string;
  avatar: string;
  unread?: number;
  status?: string;
  isRequest?: boolean;
}

const ContactItem: React.FC<ContactItemProps> = ({ name, avatar, unread, status, isRequest = false }) => {
  return (
    <div className={`flex items-start p-3 ${isRequest ? 'bg-[#242424] mb-3' : ''} rounded-xl hover:bg-[#2c2c2c] transition duration-150`}>
      
      {/* Avatar */}
      <img
        src={avatar}
        alt={name}
        className="w-10 h-10 rounded-lg object-cover mr-3 flex-shrink-0"
      />

      <div className="flex-1 min-w-0">
        <p className="text-gray-100 font-semibold truncate">{name}</p>

        {isRequest ? (
          <>
            <p className="text-sm text-gray-400 mb-2">{status}</p>
            <div className="flex space-x-2">
              <button className="flex-1 bg-blue-600 hover:bg-blue-700 text-white font-medium py-1.5 text-sm rounded-lg transition duration-150">
                Accept
              </button>
              <button className="flex-1 bg-gray-700 hover:bg-gray-600 text-gray-300 font-medium py-1.5 text-sm rounded-lg border border-gray-600 transition duration-150">
                Decline
              </button>
            </div>
          </>
        ) : (
          <div className="flex justify-between items-center h-5">
            <p className="text-sm text-gray-500">Online</p>
            {unread ? (
              <span className="bg-red-600 text-white text-xs font-bold w-5 h-5 flex items-center justify-center rounded-full">
                {unread}
              </span>
            ) : (
              <MoreHorizontal className="w-5 h-5 text-gray-500 cursor-pointer hover:text-gray-300" />
            )}
          </div>
        )}
      </div>
    </div>
  );
};

const SidebarRight: React.FC = () => {
  const totalRequests = mockRequests.length;
  const totalContacts = mockContacts.length;

  return (
    <div className="w-72 flex-shrink-0 h-screen bg-[#131313] p-2 flex flex-col">
      
      {/* Requests Section */}
      <div className="mb-4 flex-shrink-0">
        <h2 className="uppercase text-xs font-bold text-gray-400 mb-3 flex justify-between items-center">
          REQUESTS
          <span className="bg-blue-600 text-white text-xs font-bold px-2 py-0.5 rounded-full">
            {totalRequests}
          </span>
        </h2>
        <div className="space-y-3">
          {mockRequests.map(req => (
            <ContactItem
              key={req.id}
              name={req.name}
              avatar={req.avatar}
              status={req.status}
              isRequest={true}
            />
          ))}
        </div>
      </div>

      {/* Contacts Section */}
      <div className="flex-1 ">
        <h2 className="uppercase text-xs font-bold text-gray-400 mb-3 flex justify-between items-center">
          CONTACTS
          <span className="bg-gray-700 text-gray-300 text-xs font-bold px-2 py-0.5 rounded-full">
            {totalContacts}
          </span>
        </h2>
        <div className="space-y-1">
          {mockContacts.map(contact => (
            <ContactItem
              key={contact.id}
              name={contact.name}
              avatar={contact.avatar}
              unread={contact.unread}
            />
          ))}
        </div>
      </div>

    </div>
  );
};

export default SidebarRight;
